*{box-sizing:border-box}
:root{
  --bg:#0b1220;           /* navy base (sidebar) */
  --bg-soft:#0f172a;      /* slate-900 */
  --surface:#0b1220;      /* deep */
  --content:#0b1220;
  --text:#0f172a;         /* body text */
  --muted:#6b7280;        /* slate-500 */
  --border:#e5e7eb;       /* gray-200 */
  --card:#ffffff;         /* white */
  --brand:#0ea5e9;        /* sky-500 */
  --brand-600:#0284c7;    /* sky-600 */
  --accent:#f97316;       /* orange-500 */
  --accent-600:#ea580c;   /* orange-600 */
  --success:#22c55e;      /* green-500 */
}
body{margin:0;font-family:ui-sans-serif,system-ui,"Segoe UI",Roboto,Helvetica,Arial;background:#f6f8fb;color:var(--text)}
.container{max-width:1200px;margin:0 auto;padding:24px}
.card{background:var(--card);border:1px solid var(--border);border-radius:14px;box-shadow:0 1px 2px rgba(2,6,23,.04)}
.btn{display:inline-flex;align-items:center;justify-content:center;padding:8px 12px;border-radius:10px;border:1px solid var(--border);background:#fff;cursor:pointer;transition:all .15s ease}
.btn:hover{background:#f9fafb}
.btn-primary{background:var(--accent);border-color:var(--accent);color:#fff}
.btn-primary:hover{background:var(--accent-600);border-color:var(--accent-600)}
.btn-ghost{background:transparent;border-color:transparent}
.btn-ghost:hover{background:#f3f4f6}
.badge{display:inline-flex;align-items:center;padding:2px 8px;border-radius:9999px;font-size:12px}
.badge-warn{background:#fff7ed;color:#9a3412;border:1px solid #fed7aa}
.badge-live{color:var(--success);background:#ecfdf5;border:1px solid #a7f3d0;padding:2px 8px;border-radius:9999px}
.grid{display:grid;gap:16px}

/* Panel Layout */
.panel-root{display:flex;min-height:100vh;background:#f6f8fb}
.panel-aside{width:272px;background:linear-gradient(180deg,var(--bg),#0a1327);color:#e2e8f0;display:flex;flex-direction:column;border-right:1px solid #0b1a2e}
.brand{font-weight:800;font-size:18px;line-height:1.2;padding:22px 20px;border-bottom:1px solid #0b1a2e}
.nav{display:flex;flex-direction:column;padding:10px}
.nav-item{padding:10px 12px;border-radius:12px;color:#cbd5e1;text-decoration:none;border:1px solid transparent}
.nav-item:hover{background:#0f1a30;border-color:#23324b;color:#fff}
.nav-item.active{background:rgba(14,165,233,.12);border-color:rgba(14,165,233,.35);color:#e0f2fe}
.aside-foot{margin-top:auto;padding:12px 16px;color:#94a3b8;font-size:12px;border-top:1px solid #0b1a2e}
.panel-main{flex:1;display:flex;flex-direction:column}
.panel-header{height:64px;display:flex;align-items:center;justify-content:space-between;padding:0 24px;background:#ffffff;border-bottom:1px solid var(--border);position:sticky;top:0;z-index:10}
.hdr-title{font-weight:700}
.panel-content{padding:24px}

/* Table */
.table{width:100%;border-collapse:separate;border-spacing:0;background:var(--card);border:1px solid var(--border);border-radius:14px;overflow:hidden}
.table thead th{background:#f8fafc;color:#334155;text-align:left;font-weight:600;font-size:12px;letter-spacing:.02em;padding:12px;border-bottom:1px solid var(--border)}
.table tbody td{padding:14px;border-bottom:1px solid #f1f5f9;font-size:14px;color:#111827}
.table tbody tr:last-child td{border-bottom:0}
.actions{display:flex;gap:8px}
.empty{padding:32px;text-align:center;color:var(--muted)}

